import React from "react";
import ViewMorePresenter from "./ViewMorePresenter";
import { movieApi, tvApi } from "../../api";

export default class extends React.Component {
  state = {
    results: null,
    loading: true,
    error: null,
    page: 3,
    scroll: false,
  };

  async getData() {
    const {
      match: {
        params: { type, subject, query },
      },
    } = this.props;
    // console.log(`type:${type} , subject:${subject}, query:${query}`);

    try {
      if (type === "movie") {
        let movies1 = [];
        let movies2 = [];
        if (subject === "upComing") {
          ({
            data: { results: movies1 },
          } = await movieApi.upComing(1));
          ({
            data: { results: movies2 },
          } = await movieApi.upComing(2));
        } else if (subject === "nowPlaying") {
          ({
            data: { results: movies1 },
          } = await movieApi.nowPlaying(1));
          ({
            data: { results: movies2 },
          } = await movieApi.nowPlaying(2));
        } else if (subject === "popular") {
          ({
            data: { results: movies1 },
          } = await movieApi.popular(1));
          ({
            data: { results: movies2 },
          } = await movieApi.popular(2));
        } else if (subject === "search") {
          ({
            data: { results: movies1 },
          } = await movieApi.search(query, 1));
          ({
            data: { results: movies2 },
          } = await movieApi.search(query, 2));
        }
        const movies = [...movies1, ...movies2];
        this.setState({
          results: movies,
        });
      } else if (type === "show") {
        let shows1 = [];
        let shows2 = [];
        if (subject === "popular") {
          ({
            data: { results: shows1 },
          } = await tvApi.popular(1));
          ({
            data: { results: shows2 },
          } = await tvApi.popular(2));
        } else if (subject === "topRated") {
          ({
            data: { results: shows1 },
          } = await tvApi.topRated(1));
          ({
            data: { results: shows2 },
          } = await tvApi.topRated(2));
        } else if (subject === "airingToday") {
          ({
            data: { results: shows1 },
          } = await tvApi.airingToday(1));
          ({
            data: { results: shows2 },
          } = await tvApi.airingToday(2));
        }
        const shows = [...shows1, ...shows2];
        this.setState({
          results: shows,
        });
      }
    } catch {
      this.setState({ error: "Can not find View More Data from API" });
    } finally {
      this.setState({ loading: false });
    }
  }
  async getDataMore(page) {
    const {
      match: {
        params: { type, subject, query },
      },
    } = this.props;
    const { results, scroll } = this.state;
    // dconsole.log(`${type} and ${subject} and ${query}`);
    try {
      if (scroll) {
        if (type === "movie") {
          let movies1 = [];

          if (subject === "upComing") {
            ({
              data: { results: movies1 },
            } = await movieApi.upComing(page));
          }
          if (subject === "nowPlaying") {
            ({
              data: { results: movies1 },
            } = await movieApi.nowPlaying(page));
          }
          if (subject === "popular") {
            ({
              data: { results: movies1 },
            } = await movieApi.popular(page));
          }
          if (subject === "search") {
            ({
              data: { results: movies1 },
            } = await movieApi.search(query, page));
          }
          const movies = [...results, ...movies1];
          // console.log(`${type} and ${subject} and ${page}`);
          // console.log(movies1);

          /*  ÎßåÏïΩ movies1.lengthÍ∞Ä 0Ïù¥Î©¥ (ÎçîÏù¥ÏÉÅ Í∞ÄÏ†∏Ïò¨ Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏúºÎ©¥)
            ÌéòÏù¥ÏßÄÏóÖÍ∑∏Î†àÏù¥Îìú ÏïàÌïòÍ≥† Ïä§ÌÖåÏù¥Ìä∏ ÏóÖÎç∞Ïù¥Ìä∏ÎèÑ ÏïàÌïòÍ≥† Í∑∏ÎåÄÎ°ú ÎëîÎã§ */
          if (movies1.length > 0) {
            this.setState((current) => ({
              results: movies,
              scroll: false,
              page: current.page + 1,
            }));
          }
        } else if (type === "show") {
          let shows1 = [];
          if (subject === "popular") {
            ({
              data: { results: shows1 },
            } = await tvApi.popular(page));
          }
          if (subject === "topRated") {
            ({
              data: { results: shows1 },
            } = await tvApi.topRated(page));
          }
          if (subject === "airingToday") {
            ({
              data: { results: shows1 },
            } = await tvApi.airingToday(page));
          }
          const movies = [...results, ...shows1];

          this.setState((current) => ({
            results: movies,
            scroll: false,
            page: current.page + 1,
          }));
        }
      }
    } catch {
      this.setState({ error: "Can not find Data from APIzz" });
    } finally {
      this.setState({ loading: false });
    }
  }
  infiniteScroll(e) {
    const { clientHeight, scrollHeight } = document.documentElement;

    const { results, scroll } = this.state;
    // if (results && scrollTop + clientHeight >= scrollHeight - 600) {
    if (results && window.scrollY + clientHeight >= scrollHeight - 600) {
      // „Ñ¥ window.scrollY Î•º ÏÇ¨Ïö©ÌïòÎ©¥ Î™®Î∞îÏùºÏóêÏÑú ÏûëÎèôÏù¥ ÎêòÎçîÎùº + IE9 Ïù¥ÌïòÏóêÏÑúÎäî pageOffsetÏùÑ Ïç® Ï§òÏïºÌïúÎã§
      // „Ñ¥ stateÏùò results Í∞íÏùÑ Ï≤¥ÌÅ¨Ìï¥Ï£ºÏßÄ ÏïäÏúºÎ©¥
      // „Ñ¥ Ïä§ÌÅ¨Î°§Ïù¥ ÎÇ¥Î†§Í∞ÄÏûàÎäî ÏÉÅÌÉúÏóêÏÑú ÏÉàÎ°úÍ≥†Ïπ®Ïãú getData Í∞íÏùÑ Í∞ÄÏ†∏Ïò§Í∏∞ Ïù¥Ï†ÑÏóê
      // „Ñ¥ getDataMoreÎ•º Ïã§ÌñâÌï¥Î≤ÑÎ¶∞Îã§ (Í∑∏Îü¨Î©¥ÏÑú Ïò§Î•òÍ∞Ä Î∞úÏÉùÌïúÎã§)
      // „Ñ¥
      if (scroll === false) {
        // „Ñ¥ Ïù¥Î†áÍ≤å Í±∏Îü¨Ï£ºÏßÄ ÏïäÏúºÎ©¥ Ïä§ÌÅ¨Î°§Ïù¥ ÎÇ¥Î†§Í∞ÄÏÑú Ï∂îÍ∞ÄÎ°úÎìúÌï†Îïå Î°úÎìúÍ∞Ä ÏôÑÎ£åÎê†ÎïåÍπåÏßÄ (falseÎ°ú ÎèåÏïÑÏò¨ÎïåÍπåÏßÄ)
        // „Ñ¥ Í≥ÑÏÜç this.setStateÎ•º Ïã§ÌñâÏãúÏºúÏÑú ÌïÑÏöîÏóÜÎäî Î†åÎçîÎßÅÏùÑ Î™áÎ≤àÏù¥Í≥† ÌïúÎã§
        this.setState({ scroll: true });
        this.getDataMore(this.state.page);
      }
    }
  }
  componentDidMount() {
    this.getData();
    window.addEventListener("scroll", this.infiniteScroll.bind(this));
  }
  componentDidUpdate() {}
  render() {
    const {
      match: {
        params: { type, subject },
      },
    } = this.props;
    const { results, loading, error } = this.state;
    //console.log(results);
    return (
      <ViewMorePresenter
        results={results}
        loading={loading}
        error={error}
        isMovie={type === "movie"}
        subject={subject}
      />
    );
  }
}
