import React from "react";
import SearchPresenter from "./SearchPresenter";
import { movieApi, tvApi } from "../../api";
import { Redirect } from "react-router";

export default class extends React.Component {
  state = {
    movieResults: null,
    tvResults: null,
    searchTerm: "",
    error: null,
    loading: false,
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { searchTerm } = this.state;
    if (searchTerm !== "") {
      this.searchByTerm(searchTerm);
      this.props.history.push(`/search?query=${searchTerm}`);
    }
  };
  updateTerm = (event) => {
    const {
      target: { value },
    } = event;
    this.setState({
      searchTerm: value,
    });
  };

  searchByTerm = async (query) => {
    try {
      this.setState({ loading: true });
      let movieResults = null;
      let tvResults = null;
      //   console.log(`query:${query}`);
      // query Í∞íÏù¥ nullÏù¥Î©¥ , null Í∞íÏùÑ Î∞òÌôòÌï¥ÏÑú Í≤ÄÏÉâ Ï¥àÍ∏∞ÏÉÅÌÉúÍ∞Ä ÎêòÍ≤å ÎßåÎì§Í∏∞ ÏúÑÌïú Î°úÏßÅ
      // movieResults = [] Î°ú Ï¥àÍ∏∞ÌôîÎ•º ÌïòÎ©¥ presenterÏóêÏÑú ÏûàÎäîÍ±∏Î°ú Ïù∏ÏãùÏùÑÌï¥ÏÑú
      // dataÎ•º Í∞ÄÏ†∏Ïò§ÏßÄ Î™ªÌïúÍ±∏Î°ú ÌåêÎã®Ìï¥ÏÑú Î©îÏÑ∏ÏßÄÎ•º ÎùÑÏö¥Îã§.
      if (query) {
        ({
          data: { results: movieResults },
        } = await movieApi.search(query));
        ({
          data: { results: tvResults },
        } = await tvApi.search(query));
      }
      this.setState({
        movieResults,
        tvResults,
      });
    } catch {
      this.setState({
        error: "Can't find results",
      });
    } finally {
      this.setState({
        loading: false,
      });
    }
  };
  queryCheck() {
    const {
      location: { search },
    } = this.props;
    const params = new URLSearchParams(search);
    const query = params.get("query");
    //   console.log(`query: ${query}`);
    if (query === null) {
      // ÌòÑÏû¨ queryÍ∞Ä ÏïÑÎ¨¥Í∞íÎèÑ ÏóÜÏùÑÎïå ! searchTerm Ï¥àÍ∏∞ÌôîÏãúÏºúÏ£ºÍ≥† Î¶¨Î†åÎçîÎßÅ Í∑∏Î¶¨Í≥† url Ïù¥Îèô!
      this.setState({ searchTerm: "" });
      this.props.history.push("/search");
    } else {
      this.setState({ searchTerm: query });
    }
    this.searchByTerm(query);
  }
  componentDidMount() {
    this.queryCheck();
  }
  componentDidUpdate(prevProps) {
    const {
      location: { search: prevSearch },
    } = prevProps;
    const {
      location: { search: nowSearch },
    } = this.props;
    // console.log(prevSearch, nowSearch);

    if (prevSearch !== nowSearch) {
      this.queryCheck();
    }
  }
  render() {
    const { movieResults, tvResults, searchTerm, error, loading } = this.state;
    return (
      <SearchPresenter
        movieResults={movieResults}
        tvResults={tvResults}
        searchTerm={searchTerm}
        error={error}
        loading={loading}
        handleSubmit={this.handleSubmit}
        updateTerm={this.updateTerm}
      />
    );
  }
}
